body {
    background-color: whitesmoke;
}

header {
    display: flex;
}

img {
    width: 10%;
    margin: auto;
    margin-right: 0;
}

h1 {
    margin: auto;
    margin-left: 0;
    font-size: 45px;
    font-weight: 100;
    font-family: sans-serif;
}

h3 {
    font-family: sans-serif;
    font-size: 20px;
    font-weight: 70;
}

.todo-list {
    background-color: white;
    margin: auto;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #efefef;
    box-shadow: 
        0 1px 1px rgba(0,0,0,.15),
        0 10px 0 -5px #eee,
        0 10px 1px -4px rgba(0,0,0,.15),
        0 20px 0 -10px #eee,
        0 20px 1px -9px rgba(0,0,0,.15);
    padding: 30px;
}

.todo-item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 30px 20px 0;
    width: 100%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

input[type=checkbox]:focus {
    outline: 0;
}